
html,body{width:100%;height:100%
}
.container{width:100%;height:100%;background:url("../images/cubes.png");background-color:#4a90e8;font:14px "MicrosoftYaHei Bold", Helvetica, Arial, sans-serif;margin:0;padding:0;position:relative
}
a{text-decoration:none
}
.container{z-index:10;max-width:45rem;margin:0 auto;text-align:center;min-height:100%;position:relative
}
.container .main-top img{display:block;margin:0 auto
}
.container .main-top .left{padding-top:60px
}
.container .main-top .left .logo{width:5em;height:5em
}
.container .main-top .left .light{z-index:-1;width:12em;margin-top:-120px;position:relative;animation:9.5s linear 0s normal none infinite rotate;-webkit-animation:9.5s linear 0s normal none infinite rotate;-moz-animation:9.5s linear 0s normal none infinite rotate
}
.container .main-top .left .title{margin-top:-20px;width:80%
}
.container .main-top .left .content-c{display:none
}
.container .main-top .left .content-m{width:90%;margin:10% auto
}
.container .main-top .left .btn{width:60%;line-height:50px;text-align:center;height:50px;font-size:22px;text-decoration:none;letter-spacing:.07em;border-radius:5px;display:block
}
.container .main-top .left .btn-ios{background-color:#c9c9c9;color:#fff;border-bottom:4px solid #616161;cursor:not-allowed;margin:0 auto
}
.container .main-top .left .btn-android{cursor:pointer;background-color:#ffd14f;color:#fff;border-bottom:4px solid #ff6d33;margin:5% auto
}
.container .main-top .left .btn-android:active,.container .main-top .left .btn-android:hover{position:relative;top:1px;border-bottom:0
}
.container .main-top .left .btn-c{display:none
}
.container .main-top .right{display:none
}
.container .main-center,.container .main-bottom{display:none
}
@keyframes rotate{
from{transform:rotate(0deg)
}
to{transform:rotate(360deg)
}
}
footer{position:absolute;bottom:0;left:0;background-color:#fec300;text-align:center;width:100%
}
footer p,footer a{display:inline-block;height:1.6em;line-height:1.6em;color:#fff
}
footer a{cursor:pointer;margin:0 .5em
}
footer a:hover{color:#4a90e8
}
.erwei{display:none
}
#mask{position:absolute;left:0;top:0;background:rgba(0,0,0,0.5);z-index:2
}



